body {
  font-family: 'Nunito Sans', Arial, sans-serif;
  background-color: #42756a;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.app-container {
  width: 100%;
  max-width: 500px;
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background: #e1dcd1;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
  padding: 32px 24px;
}

/* Add below this line */
.product-form {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 16px 24px;
  width: 100%;
}

.form-row {
  display: contents;
}

.form-label {
  grid-column: 1;
  font-weight: 600;
  margin-bottom: 12px;
  text-align: right;
  padding-right: 16px;
}

.form-input {
  grid-column: 2;
  margin-bottom: 12px;
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #bbb;
  font-size: 1rem;
}

.product-form input,
.product-form select {
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #bbb;
  font-size: 1rem;
}